/**
 * Textures
 */
const textureLoader = new THREE.TextureLoader();

function MoveTexture() {
  // this.parent = parent
  this.size = 64 * 1;
  this.size = Math.floor(sizes.width / 2);
  this.maxAge = settings.age; // 120
  this.radius = settings.radius;
  this.trail = [];

  this.initTexture = () => {
    this.maxAge = settings.age; // 120
    this.radius = settings.radius;
    this.trail = [];

    this.canvas = document.createElement("canvas");
    this.canvas.width = this.canvas.height = this.size;
    this.ctx = this.canvas.getContext("2d");
    this.ctx.fillStyle = "black";
    this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);

    this.texture = new THREE.Texture(this.canvas);

    this.canvas.id = "touchTexture";
    this.canvas.style.width =
      this.canvas.style.height = `${this.canvas.width}px`;
  };

  this.initTexture();

  this.update = (delta) => {
    this.ctx.globalCompositeOperation = "source-over";
    this.clear();
    this.ctx.globalCompositeOperation = "screen";

    // age points
    this.trail.forEach((point, i) => {
      point.age++;
      // remove old
      if (point.age > this.maxAge) {
        this.trail.splice(i, 1);
      }
    });

    this.trail.forEach((point, i) => {
      this.drawTouch(point);
    });

    this.texture.needsUpdate = true;
  };

  this.clear = () => {
    this.ctx.fillStyle = "black";
    this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
  };

  this.addTouch = (point) => {
    let force = 0;
    const last = this.trail[this.trail.length - 1];
    let dir = new THREE.Vector2();
    if (last) {
      const dx = last.x - point.x;
      const dy = last.y - point.y;
      const dd = dx * dx + dy * dy;
      force = Math.min(dd * 10000, 1);

      const v = new THREE.Vector2(point.x, point.y);
      const u = last ? new THREE.Vector2(last.x, last.y) : v;
      v.sub(u).normalize();
      dir.x = (v.x * 0.5 + 0.5) * 255;
      dir.y = (v.y * 0.5 + 0.5) * 255;
    }
    this.trail.push({ x: point.x, y: point.y, age: 0, force, dir });
    // console.log(this.trail)
  };

  this.drawTouch = (point) => {
    const pos = {
      x: point.x * this.size,
      y: (1 - point.y) * this.size,
    };

    // const last = this.trail[this.trail.length - 1];
    // let v = new THREE.Vector2(pos.x, pos.y)
    // let u = last ? new THREE.Vector2(last.x, last.y) : v

    let intensity = 1;
    // if (point.age < this.maxAge * 0.3) {
    // 	intensity = .0 + 1. * easeOutSine(point.age / (this.maxAge * 0.3), 0, 1, 1);
    // } else {
    // 	intensity = easeOutSine(1 - (point.age - this.maxAge * 0.3) / (this.maxAge * 0.7), 0, 1, 1);
    // }

    let age = point.age / this.maxAge;

    // intensity *= point.force;
    // intensity = point.force * easeOutSine(mapLinear(point.age, 0, this.maxAge, 1, 0), 0, 1, 1)
    // intensity = point.force * easeOutSine(1-age, 0, 1, 1)
    intensity = point.force * (1 - age);

    let a = 0.2 - age * 0.2;
    let rf = 0.25 - age * 0.25;
    a = 0.25;
    rf = 0.25;

    const radius = this.size * this.radius * intensity;
    // const radius = this.size * this.radius * (age)*2;
    const grd = this.ctx.createRadialGradient(
      pos.x,
      pos.y,
      radius * rf,
      pos.x,
      pos.y,
      radius
    );
    // grd.addColorStop(0, `rgba(255, 255, 255, .25)`);
    // grd.addColorStop(0, 'rgba(0, 0, 0, 0.0)');
    // grd.addColorStop(0.25, `rgba(${point.dir.x}, ${point.dir.y}, 255, ${a})`);
    // grd.addColorStop(0.5, `rgba(${point.dir.x}, ${point.dir.y}, 255, ${a})`);
    // grd.addColorStop(1, 'rgba(0, 0, 0, 0.0)');

    grd.addColorStop(0, `rgba(${point.dir.x}, ${point.dir.y}, 255, ${a})`);
    grd.addColorStop(1, "rgba(0, 0, 0, 0.0)");
    // grd.addColorStop(1, 'rgba(0, 0, 0, 0.0)');

    this.ctx.beginPath();
    this.ctx.fillStyle = grd;
    this.ctx.ellipse(
      pos.x,
      pos.y,
      radius * 1,
      radius * 1,
      0,
      Math.PI * 2,
      false
    );
    this.ctx.fill();

    // let d = u.distanceTo(v)
    // for (let i = 0; i < d; i++) {
    //     let w = new THREE.Vector2(
    //         mapLinear(i/d, 0, 1, u.x, v.x),
    //         mapLinear(i/d, 0, 1, u.y, v.y)
    //     )
    // }
  };
}

function Pixels() {
  this.grid = settings.grid;
  this.width = this.grid;
  this.height = this.grid;
  this.size = this.width * this.height;
  this.data = new Uint8Array(4 * this.size);
  this.color = new THREE.Color(0xffffff);

  this.dataTexture = null;

  this.geometry = null;
  this.material = null;
  this.mesh = null;

  this.updateDataTexture = () => {
    let data = this.dataTexture.image.data;
    for (let i = 0; i < data.length; i += 4) {
      data[i] *= settings.relaxation;
      data[i + 1] *= settings.relaxation;
    }

    let gridMouseX = this.grid * mouse.x;
    let gridMouseY = this.grid * (1 - mouse.y);
    let maxDist = this.grid * settings.mouse;
    let aspect = 1;

    for (let i = 0; i < this.grid; i++) {
      for (let j = 0; j < this.grid; j++) {
        let distance = (gridMouseX - i) ** 2 / aspect + (gridMouseY - j) ** 2;
        let maxDistSq = maxDist ** 2;

        if (distance < maxDistSq) {
          let index = 4 * (i + this.grid * j);

          let power = maxDist / Math.sqrt(distance);
          power = clamp(power, 0, 10);
          power *= elasticInOut(mapLinear(distance, 0, maxDistSq, 1, 0));

          data[index] += settings.strength * 100 * mouse.vX * power;
          data[index + 1] -= settings.strength * 100 * mouse.vY * power;
        }
      }
    }

    mouse.vX *= 0.9;
    mouse.vY *= 0.9;
    this.dataTexture.needsUpdate = true;
  };

  this.setup = () => {
    this.grid = settings.grid;
    if (sizes.width > sizes.height) {
      this.width = this.grid * Math.floor(sizes.width / sizes.height);
      this.height = this.grid;
    } else {
      this.width = this.grid;
      this.height = this.grid * Math.floor(sizes.height / sizes.width);
    }
    this.size = this.width * this.height;
    this.data = new Uint8Array(4 * this.size);

    for (let i = 0; i < this.size; i++) {
      const i4 = i * 4;

      let r0 = Math.random() * 255 - 125 * 1;
      let r1 = Math.random() * 255 - 125 * 1;

      this.data[i4] = r0 * 0;
      this.data[i4 + 1] = r1 * 0;
      this.data[i4 + 2] = r0 * 0 + 255;
      this.data[i4 + 3] = 255;
    }

    this.dataTexture = new THREE.DataTexture(
      this.data,
      this.width,
      this.height
    );
    this.dataTexture.magFilter = this.dataTexture.minFilter =
      THREE.NearestFilter;
    this.needsUpdate = true;

    this.addObject();
  };

  this.addObject = () => {
    let a = new THREE.Vector2(1, 1);
    if (sizes.width > sizes.height) {
      a.x = sizes.width / sizes.height;
    } else {
      a.y = sizes.height / sizes.width;
    }

    this.geometry = new THREE.PlaneGeometry(a.x, a.y, 1, 1);
    this.material = new THREE.ShaderMaterial({
      fragmentShader: fragment,
      vertexShader: vertex,
      uniforms: {
        uTime: { value: 0 },
        uTexture: { value: textureLoader.load("./textures/melt_2440.png") },
        uDataTexture: { value: this.dataTexture },
        uMoveTexture: { value: null },
        uResolution: { value: new THREE.Vector4() },
        uGrid: { value: this.grid },
        PI: { value: Math.PI },
        uMouse: { value: new THREE.Vector2() },
      },
      side: THREE.DoubleSide, // some three.js material properties work. map, alpha, opacity, color etc. wont work (handled by shader)
    });

    this.material.uniforms.uResolution.value.x = this.width;
    this.material.uniforms.uResolution.value.y = this.height;
    this.material.uniforms.uResolution.value.z = a.x;
    this.material.uniforms.uResolution.value.w = a.y;
    // console.log(a);

    this.mesh = new THREE.Mesh(this.geometry, this.material);
    scene.add(this.mesh);
  };
}

let pixels = new Pixels();
pixels.setup();

let tex = new MoveTexture();

window.addEventListener("mousemove", (e) => {
  mouse.x = e.clientX / sizes.width;
  mouse.y = e.clientY / sizes.height;

  mouse.vX = mouse.x - mouse.prevX * 0.9;
  mouse.vY = mouse.y - mouse.prevY * 0.9;

  mouse.prevX = mouse.x;
  mouse.prevY = mouse.y;

  tex.addTouch(new THREE.Vector2(mouse.x, 1 - mouse.y));
});

